import React, { Component } from 'react';
import ScrollAnimation from 'react-animate-on-scroll';
import "animate.css/animate.min.css";

class Skills extends Component {
    constructor(props) {
        super(props);
        this.handleEducationHover = this.handleEducationHover.bind(this);
        this.handleExperienceHover = this.handleExperienceHover.bind(this);
        this.state = {
            isEducationHovering: false,
            isExperienceHovering: false,
        };
    }

    onChange = (isVisible) => {
        this.props.onViewportChange(isVisible, 'resume');
    }

    handleEducationHover() {
        this.setState(this.toggleEducationHoverState);
    }

    toggleEducationHoverState(state) {
        return {
            isEducationHovering: !state.isEducationHovering,
        };
    }

    handleExperienceHover() {
        this.setState(this.toggleExperienceHoverState);
    }

    toggleExperienceHoverState(state) {
        return {
            isExperienceHovering: !state.isExperienceHovering,
        };
    }

    render() {

        return (
            // <VisibilitySensor onChange={this.onChange} partialVisibility={true} minTopValue={800} minBottomValue={800}>
            <div className="row page" id="resume">
                <div className="d-none d-md-block col-md-3"></div>
                <div className="col-xs-12 col-sm-12 col-md-7 col-lg-7 resume-container">
                    <br className="resume-break" />
                    <br className="resume-break" />
                    <div className="education-container">
                        <ScrollAnimation animateIn='slideInUp' animateOnce={true} delay={600} duration={.5}
                        // afterAnimatedIn={() => { this.props.onViewportChange(true, 'resume') }}
                        // afterAnimatedOut={() => { this.props.onViewportChange(false, 'resume') }}
                        >
                            <div className="flex-one">
                                <h1 className="text-right education-header">skills</h1>
                            </div>
                        </ScrollAnimation>
                        {/* <div className="resume-divider"></div> */}
                        <ScrollAnimation animateIn='slideInUp' animateOnce={true} delay={600} duration={.5}
                        // afterAnimatedIn={() => { this.props.onViewportChange(true, 'resume') }}
                        // afterAnimatedOut={() => { this.props.onViewportChange(false, 'resume') }}
                        >
                            <div className="border-left flex-one education-content">
                                Javascript, Ember, React, React-Redux, HTML, CSS, Express, Node, Sequelize, Postico, Github, Web Sockets, Adobe Suite <br />
                            </div>
                        </ScrollAnimation>
                    </div>
                </div>
            </div >
            // </VisibilitySensor>
        );
    }
}

export default Skills;